import React, { useEffect, useMemo, FC } from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import { utils } from 'ethers';
import currency from 'currency.js';
import { Card, Grid, Typography, makeStyles } from '@material-ui/core';
import { Theme } from 'src/theme';
import Label from 'src/components/Label';
import LoadingBox from 'src/components/LoadingBox';
import { BTCB, BTCST } from 'src/constants/token';
import { formatBalance } from 'src/utils/format';
import { useTranslation } from 'react-i18next';
import { Token } from 'src/types/token';
interface OverviewProps {
  className?: string;
  BTCBPoolStakeAmount: any;
  BTCSTPoolStakeAmount: any;
  totalMined: any;
  btcstTotalSupply: any;
  staked: any;
  totalInPoolReward?: any;
  apy: string;
  targetToken: Token;
}

const useStyles = makeStyles((theme: Theme) => ({
  root: {},
  item: {
    padding: theme.spacing(3),
    textAlign: 'center',
    [theme.breakpoints.up('md')]: {
      '&:not(:last-of-type)': {
        borderRight: `1px solid ${theme.palette.divider}`
      }
    },
    [theme.breakpoints.down('sm')]: {
      '&:not(:last-of-type)': {
        borderBottom: `1px solid ${theme.palette.divider}`
      }
    }
  },
  valueContainer: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center'
  },
  label: {
    marginLeft: theme.spacing(1)
  },
  tau: {
    'text-transform': 'initial'
  }
}));

const Overview: FC<OverviewProps> = ({
  className,
  totalMined,
  BTCBPoolStakeAmount,
  BTCSTPoolStakeAmount,
  staked,
  totalInPoolReward,
  targetToken,
  apy,
  btcstTotalSupply,
  ...rest
}) => {
  const classes = useStyles();
  const { t } = useTranslation();
  const currentStakeAmount = useMemo(() => {
    return targetToken.symbol === BTCB.symbol
      ? BTCBPoolStakeAmount
      : BTCSTPoolStakeAmount;
  }, [targetToken, BTCBPoolStakeAmount, BTCSTPoolStakeAmount]);

  const btcstTotalStaked = useMemo(() => {
    const data =
      BTCBPoolStakeAmount.result && BTCSTPoolStakeAmount.result
        ? BTCBPoolStakeAmount.result.add(BTCSTPoolStakeAmount.result)
        : undefined;
    return data;
  }, [BTCBPoolStakeAmount.result, BTCSTPoolStakeAmount.result]);

  // console.log(btcstTotalStaked);

  return (
    <Card className={clsx(classes.root, className)} {...rest}>
      <Grid alignItems="center" container justify="space-between">
        <Grid className={classes.item} item md={3} sm={6} xs={12}>
          <Typography
            component="h2"
            gutterBottom
            variant="overline"
            color="textSecondary"
          >
            {t('stats.alltimeMined').replace("#",targetToken.name)}
          </Typography>
          <div className={classes.valueContainer}>
            <LoadingBox loading={totalMined.loading}>
              <Typography variant="h3" color="textPrimary">
                {totalMined.result
                  ? formatBalance(totalMined.result, BTCB.decimals)
                  : '-'}
              </Typography>
            </LoadingBox>
          </div>
        </Grid>
        <Grid className={classes.item} item md={3} sm={6} xs={12}>
          <Typography
            component="h2"
            gutterBottom
            variant="overline"
            color="textSecondary"
          >
            {t('stats.btcst.staked')}
          </Typography>
          <div className={classes.valueContainer}>
            <LoadingBox loading={currentStakeAmount.loading} size="25px" p={0}>
              <Typography variant="h3" color="textPrimary">
                {currentStakeAmount.result
                  ? formatBalance(currentStakeAmount.result, BTCST.decimals,8)
                  : '-'}
              </Typography>
            </LoadingBox>
          </div>
        </Grid>
        <Grid className={classes.item} item md={3} sm={6} xs={12}>
          <Typography
            component="h2"
            gutterBottom
            variant="overline"
            color="textSecondary"
          >
            {t('stats.mining.estapy')}
          </Typography>
          <div className={classes.valueContainer}>
            <LoadingBox loading={totalInPoolReward.loading}>
              <Typography variant="h3" color="textPrimary">
                {apy ? apy + '%' : '-'}
              </Typography>
            </LoadingBox>
          </div>
        </Grid>
        <Grid className={classes.item} item md={3} sm={6} xs={12}>
          <Typography
            component="h2"
            gutterBottom
            variant="overline"
            color="textSecondary"
          >
            {t('stats.btcst.boostFactor')}
          </Typography>
          <div className={classes.valueContainer}>
            <LoadingBox
              loading={
                btcstTotalSupply.loading ||
                BTCSTPoolStakeAmount.loading ||
                BTCBPoolStakeAmount.loading
              }
            >
              <Typography variant="h3" color="textPrimary">
                {btcstTotalSupply.result && btcstTotalStaked
                  ? currency(
                      utils.formatUnits(
                        btcstTotalSupply.result,
                        BTCST.decimals
                      ),
                      { symbol: '' }
                    )
                      .multiply(0.6)
                      .divide(
                        utils.formatUnits(btcstTotalStaked, BTCST.decimals)
                      )
                      .format() + 'X'
                  : '-'}
              </Typography>
            </LoadingBox>
          </div>
        </Grid>
      </Grid>
    </Card>
  );
};

Overview.propTypes = {
  className: PropTypes.string
};

export default Overview;
