import React, { FC, useEffect, useState } from 'react';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import {
  Avatar,
  Box,
  Card,
  LinearProgress,
  Typography,
  makeStyles
} from '@material-ui/core';
import useInterval from '@use-it/interval';
import { Theme } from 'src/theme';
import CircularProgress from './CircularProgress';
import Label from 'src/components/Label';
import { useTranslation } from 'react-i18next';
import { getRemainTime } from 'src/utils/format';

interface TauBTCRebaseCountdownProps {
  className?: string;
}

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    padding: theme.spacing(3),
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between'
  },
  progress: {
    margin: theme.spacing(0, 1),
    flexGrow: 1
  },
  label: {
    marginLeft: theme.spacing(1)
  },
  avatar: {
    // backgroundColor: theme.palette.secondary.main,
    // color: theme.palette.secondary.contrastText,
    height: 48,
    width: 48
  }
}));

const TauBTCRebaseCountdown: FC<TauBTCRebaseCountdownProps> = ({
  className,
  ...rest
}) => {
  const classes = useStyles();
  const [time, setTime] = useState('');
  const [percent, setPercent] = useState(0);

  useInterval(() => {
    const data = getRemainTime();
    setTime(`${data.hours}:${data.minutes}:${data.seconds}`);
    setPercent(data.progress);
  }, 1000);

  const { t } = useTranslation();
  return (
    <Card className={clsx(classes.root, className)} {...rest}>
      <Box flexGrow={1}>
        <Typography
          component="h3"
          gutterBottom
          variant="overline"
          color="textSecondary"
        >
          {t('stats.tbtc.nextRebase')}
        </Typography>
        <Box display="flex" alignItems="center" flexWrap="wrap">
          <Typography variant="h3" color="textPrimary">
            {time}
          </Typography>
        </Box>
      </Box>
      <CircularProgress value={percent} className={classes.avatar} />
    </Card>
  );
};

TauBTCRebaseCountdown.propTypes = {
  className: PropTypes.string
};

export default TauBTCRebaseCountdown;
