import React from 'react';
import type { FC } from 'react';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import {
  Avatar,
  Box,
  Card,
  Typography,
  makeStyles
} from '@material-ui/core';
import AttachMoneyIcon from '@material-ui/icons/AttachMoney';
import type { Theme } from 'src/theme';
import Label from 'src/components/Label';
import LoadingBox from 'src/components/LoadingBox';
import { BigNumber } from '@ethersproject/bignumber';
import { formatBalance } from 'src/utils/format';
import { useTranslation } from 'react-i18next';
interface TauBTCTotalSupplyProps {
  className?: string;
  totalSupply?:any;
  chefStats?:any;
}

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    padding: theme.spacing(3),
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between'
  },
  label: {
    marginLeft: theme.spacing(1)
  },
  avatar: {
    backgroundColor: theme.palette.secondary.main,
    color: theme.palette.secondary.contrastText,
    height: 48,
    width: 48
  },
  valueContainer: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center'
  },
  tau:{
    'text-transform': 'initial'
  }
}));

const TauBTCTotalSupply: FC<TauBTCTotalSupplyProps> = ({ className,totalSupply,chefStats, ...rest }) => {
  const classes = useStyles();
  const {t} = useTranslation();
  let circulatingSupply;
  if ( chefStats && chefStats.result && totalSupply && totalSupply.result){
    circulatingSupply = totalSupply.result.sub(chefStats.result.balanceForChef);
    if (chefStats.result.stakedTBTC){
      circulatingSupply = circulatingSupply.add(chefStats.result.stakedTBTC);
    }
  }
  return (
    <Card
      className={clsx(classes.root, className)}
      {...rest}
    >
      <Box flexGrow={1}>
        <Typography
          component="h3"
          gutterBottom
          variant="overline"
          color="textSecondary"
        >
          <i className={classes.tau}>&tau;</i>{t("stats.tbtc.totalSupply")}
        </Typography>
        <Box
          display="flex"
          alignItems="center"
          flexWrap="wrap"
        >
          <div className={classes.valueContainer}>
            <LoadingBox loading={totalSupply.loading||chefStats.loading} p={0} size={'25px'}>
              <Typography
                variant="h3"
                color="textPrimary"
              >
                {/* {data.currency} */}
                {circulatingSupply ? 
                formatBalance(circulatingSupply,9): '-'}
              </Typography>
            </LoadingBox> 
          </div>
        </Box>
      </Box>
      <Avatar className={classes.avatar}>
        <AttachMoneyIcon />
      </Avatar>
    </Card>
  );
};

TauBTCTotalSupply.propTypes = {
  className: PropTypes.string
};

export default TauBTCTotalSupply;
