import React, {
  useCallback,
  useEffect,
  useState
} from 'react';
import type { FC } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import clsx from 'clsx';
import numeral from 'numeral';
import PerfectScrollbar from 'react-perfect-scrollbar';
import PropTypes from 'prop-types';
import {
  Box,
  Button,
  Card,
  CardHeader,
  Divider,
  Table,
  TableBody,
  TableCell,
  TableRow,
  Typography,
  colors,
  makeStyles
} from '@material-ui/core';
import NavigateNextIcon from '@material-ui/icons/NavigateNext';
import axios from 'src/utils/axios';
import useIsMountedRef from 'src/hooks/useIsMountedRef';
import GenericMoreButton from 'src/components/GenericMoreButton';
import type { Theme } from 'src/theme';
import type { RebaseRecord } from 'src/types/reports';
import CircularProgress from './CircularProgress';
import LoadingBox from 'src/components/LoadingBox';
import { formatBalance } from 'src/utils/format';
import Label from 'src/components/Label';
import { BigNumber } from '@ethersproject/bignumber';
import { useTranslation } from 'react-i18next';
interface TauBTCRebaseRecordProps {
  className?: string;
  rebaseRecords?:any;
}

const useStyles = makeStyles((theme: Theme) => ({
  root: {},
  image: {
    flexShrink: 0,
    height: 56,
    width: 56
  },
  label: {
    // marginLeft: theme.spacing(1)
  },
  subscriptions: {
    fontWeight: theme.typography.fontWeightMedium
  },
  value: {
    color: colors.green[600],
    fontWeight: theme.typography.fontWeightMedium
  }
}));

const TauBTCRebaseRecord: FC<TauBTCRebaseRecordProps> = ({ rebaseRecords,className, ...rest }) => {
  const classes = useStyles();
  const isMountedRef = useIsMountedRef();

  // console.log("rebaseRecords",rebaseRecords);
  const {t} = useTranslation();
  let records=[];
  if (rebaseRecords && !rebaseRecords.loading){
    const list = rebaseRecords.result || [];
    for (let i=0;i<5;i++){
      if (i< list.length){
        const element = list[i];
        
        records.push({data:element,rate:0,timestampSec:element.timestampSec.toNumber()});
        if (element.requestedSupplyAdjustment.isZero()){
          records[i].rate = BigNumber.from(0);
        }else{
          console.log(`epoch:${element.epoch},element.requestedSupplyAdjustment:${element.requestedSupplyAdjustment},el.newBalance:${element.newBalance}`);
          records[i].rate = element.requestedSupplyAdjustment.mul(10000).div(
            element.requestedSupplyAdjustment.add(element.newBalance));
        }
        // console.log("rate",records[i].rate)
      }else{
        records.push({
          empty:true,
          timestampSec:i
        });
      } 
    }
  }else{
    for(let i=0;i<5;i++){
      records.push({loading:true,data:"-",timestampSec:i});
    }
  }
  // console.log("records",records);
  return (
    <Card
      className={clsx(classes.root, className)}
      {...rest}
    >
      <CardHeader
        // action={<GenericMoreButton />}
        title={t("stats.tbtc.rebase.records")}
      />
      <Divider />
      <PerfectScrollbar>
        <Box minWidth={700}>
          <Table>
            <TableBody>
              {records.map((record) => (
              record && record.loading ?(
              <TableRow
                hover
                key={record.timestampSec}
              >
                <TableCell>
                  <Box
                    display="flex"
                    alignItems="center"
                  >
                    {/* <img
                      alt="Product"
                      className={classes.image}
                      src={product.image}
                    /> */}
                    <Box ml={2}>
                      <Typography
                        variant="h6"
                        color="textPrimary"
                      >
                        {t("stats.tbtc.rebase.epoch")}
                      </Typography>
                      <Typography
                        variant="body2"
                        color="textSecondary"
                      >
                        {' '}
                        {''}
                      </Typography>
                    </Box>  
                    <Box ml={2}>
                      <Typography
                        variant="h6"
                        color="textPrimary"
                      >
                        {''}
                      </Typography>
                      <Typography
                        variant="body2"
                        color="textSecondary"
                      >
                        {' '} 
                      </Typography>
                    </Box>
                  </Box>
                </TableCell>
                <TableCell>
                <LoadingBox loading={record.loading} p={0} size={'25px'}>
                  <Typography
                    variant="h6"
                    color="textPrimary"
                  >
                    {t("stats.tbtc.rebase.price")}
                  </Typography>
                  <Typography
                    noWrap
                    variant="body2"
                    color="textSecondary"
                  >
                    {' '}
                  </Typography>
                </LoadingBox> 
                </TableCell>
                <TableCell>
                  <Box
                    display="flex"
                    alignItems="center"
                    justifyContent="flex-end"
                  >
                    <Box mr={2}>
                      <Typography
                        align="right"
                        variant="h6"
                        color="textPrimary"
                      >
                       {" "}
                      </Typography>
                      <Typography
                        variant="body2"
                        color="textSecondary"
                      >
                        {t("stats.tbtc.rebase.adjustRate")}
                      </Typography>
                    </Box>
                    <CircularProgress value={0} />
                  </Box>
                </TableCell>
              </TableRow> 
              )
              :(
              <TableRow
                hover
                key={record.timestampSec}
              >                      
                <TableCell>
                <Box
                  display="flex"
                  alignItems="center"
                >
                  {/* <img
                    alt="Product"
                    className={classes.image}
                    src={product.image}
                  /> */}
                  <Box ml={2}>
                    <Typography
                      variant="h6"
                      color="textPrimary"
                    >
                      {t("stats.tbtc.rebase.epoch")}
                    </Typography>
                    <Typography
                      variant="body2"
                      color="textSecondary"
                    >
                      {' '}
                      {record.empty? "-":record.data.epoch+""}
                    </Typography>
                  </Box>  
                  <Box ml={2}>
                    <Typography
                      variant="h6"
                      color="textPrimary"
                    >
                      {record.empty? "-":record.data.requestedSupplyAdjustment && record.data.requestedSupplyAdjustment.gt(0)? 
                      t('stats.rebase.records.inflation'):
                      record.data.requestedSupplyAdjustment.lt(0)?t('stats.rebase.records.deflation'):
                      t('stats.rebase.records.noaction')}
                    </Typography>
                    <Typography
                      variant="body2"
                      color="textSecondary"
                    >
                      <span className={classes.subscriptions}>
                        {record.empty? "-":numeral(formatBalance(record.data.requestedSupplyAdjustment,9,8)).format('0,0.00000000')}
                      </span>
                      {' '}
                      &tau;Bitcoin
                    </Typography>
                  </Box>
                </Box>
              </TableCell>
              <TableCell>
                <Typography
                  variant="h6"
                  color="textPrimary"
                >
                  {t("stats.tbtc.rebase.price")}
                </Typography>
                <Typography
                  noWrap
                  variant="body2"
                >
                  <Label
                    className={classes.label}
                    color={record.empty? "secondary":
                    Number(formatBalance(record.data.exchangeRate,18,2))>1? "success":
                    Number(formatBalance(record.data.exchangeRate,18,2))<1? "error":"secondary"}
                  >
                    {record.empty? "-":numeral(formatBalance(record.data.exchangeRate,18,2)).format(`0,0.00`)}
                  </Label>
                  
                  {' '}
                </Typography>
              </TableCell>
              <TableCell>
              <Box
                display="flex"
                alignItems="center"
                justifyContent="flex-end"
              >
                <Box mr={2}>
                  <Typography
                    align="right"
                    variant="h6"
                    color="textPrimary"
                  >
                    {record.empty || !record.rate? " ":formatBalance(record.rate,2,4)}
                    %
                  </Typography>
                  <Typography
                    variant="body2"
                    color="textSecondary"
                  >
                    {t("stats.tbtc.rebase.adjustRate")}
                  </Typography>
                </Box>
                <CircularProgress value={record.empty || !record.rate ?0: 
                  record.rate.gt(0)?
                  Number(formatBalance(record.rate,2,4)):
                  100+Number(formatBalance(record.rate,2,4)) }/>
              </Box>
            </TableCell>
            </TableRow> 
              )   
              ))}
            </TableBody>
          </Table>
        </Box>
      </PerfectScrollbar>
      <Box
        p={2}
        display="flex"
        justifyContent="flex-end"
      >
        {/* <Button
          component={RouterLink}
          size="small"
          to="#"
          endIcon={<NavigateNextIcon />}
        >
          See all
        </Button> */}
      </Box>
    </Card>
  );
}

TauBTCRebaseRecord.propTypes = {
  className: PropTypes.string
};

export default TauBTCRebaseRecord;
