import React, {
  useCallback,
  useEffect,
  useState,
  FC
} from 'react';
import { Link as RouterLink } from 'react-router-dom';
import clsx from 'clsx';
import moment from 'moment';
import PropTypes from 'prop-types';
import {
  Avatar,
  Box,
  Card,
  CardHeader,
  Divider,
  Link,
  List,
  ListItem,
  ListItemAvatar,
  ListItemText,
  Typography,
  makeStyles
} from '@material-ui/core';
import { Theme } from 'src/theme';
import useIsMountedRef from 'src/hooks/useIsMountedRef';
import GenericMoreButton from 'src/components/GenericMoreButton';
import { BigNumber } from 'ethers';
import { formatBalance } from 'src/utils/format';
import LoadingBox from 'src/components/LoadingBox';
import { BTCST } from 'src/constants/token';
import { useTranslation } from 'react-i18next';
import { Token } from 'src/types/token';
interface   RecordsProps {
  className?: string;
  loading?: boolean;
  records: any[];
  title: string;
  stake: any;
  apy: string;
  targetToken:Token;
}

const useStyles = makeStyles((theme: Theme) => ({
  root: {},
  item: {
    padding: theme.spacing(3),
    flexGrow: 1,
    '&:first-of-type': {
      borderRight: `1px solid ${theme.palette.divider}`
    }
  }
}));
const RecordItem: FC<any> = ({record, index,targetToken}) => {
  const rewardAmount = record.rewardAmount;
  const stakedLowestWaterMark = record.stakedLowestWaterMark;
  const { t } = useTranslation();
  let logo;
  if (targetToken.name=="BTCB"){
    logo = "tokens/BTCB.png";
  }else if(targetToken.name == "BTCST"){
    logo = "tokens/BTCST-Small.png";
  }
  return (
    <ListItem
      divider={index < 5}
      key={record.id}
    >
      <ListItemAvatar>
        <Avatar
          alt="Customer"
          component={RouterLink}
          src={logo}
          to="#"
        >
          {/* {getInitials(record.customer.name)} */}
        </Avatar>
      </ListItemAvatar>
      <ListItemText
        disableTypography
        primary={(
          <Typography
            color="textPrimary"
            variant="h6"
          >
            {t("stats.mining.daily.reward")}: {rewardAmount.gt(BigNumber.from(0)) ? formatBalance(rewardAmount || 0): "N/A"}
          </Typography>
        )}
        secondary={(
          <Typography
            variant="body2"
            color="textSecondary"
          >
            {t("stats.mining.daily.staked")}: {stakedLowestWaterMark.gt(BigNumber.from(0)) ? formatBalance(stakedLowestWaterMark || 0,17,1): "N/A"}
          </Typography>
        )}
      />
      <Typography
        color="textSecondary"
        noWrap
        variant="caption"
      >
        {new Date(record.timeKey * 1000).toLocaleDateString('zh-cn')}
      </Typography>
    </ListItem>
  )
}

const Records: FC<RecordsProps> = ({ className,targetToken, title, stake, apy, loading = false, records = [], ...rest }) => {
  const classes = useStyles();
  const isMountedRef = useIsMountedRef();
  const { t } = useTranslation();
  return (
    <Card
      className={clsx(classes.root, className)}
      {...rest}
    >
      <CardHeader
        title={title}
        // action={<GenericMoreButton />}
      />
      <Divider />
      <List disablePadding>
        {records.map((record, i) => (
          <RecordItem record={record} key={i} index={i} targetToken={targetToken}/>
        ))}
      </List>
    </Card>
  );
}

Records.propTypes = {
  className: PropTypes.string
};

export default Records;
