import React from 'react';
import type { FC } from 'react';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import {
  Avatar,
  Box,
  Card,
  Typography,
  makeStyles
} from '@material-ui/core';
import AttachMoneyIcon from '@material-ui/icons/AttachMoney';
import type { Theme } from 'src/theme';
import LoadingBox from 'src/components/LoadingBox';
import currency from 'currency.js';
import { useTranslation } from 'react-i18next';
import isEmpty from 'lodash/isEmpty'; 
import { useRecoilValue } from 'recoil';
import { tokenPriceLoadingAtom } from 'src/atoms';
import { BTCB } from 'src/constants/token';
import { getPriceBySymbol } from 'src/utils/price';
import { Token } from 'src/types/token';

interface RoiPerCustomerProps {
  className?: string;
  tokenPrice?:any;
  token: Token;
  title: any;
}

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    color: theme.palette.secondary.contrastText,
    backgroundColor: theme.palette.secondary.main,
    padding: theme.spacing(3),
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between'
  },
  valueContainer: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center'
  },
  avatar: {
    backgroundColor: theme.palette.secondary.contrastText,
    color: theme.palette.secondary.main,
    height: 48,
    width: 48
  },
  tau:{
    'text-transform': 'initial'
  }
}));

const RoiPerCustomer: FC<RoiPerCustomerProps> = ({ tokenPrice,className, token, title, ...rest }) => {
  const classes = useStyles();
  const {t} = useTranslation();
  const tokenPriceLoading = useRecoilValue(tokenPriceLoadingAtom);
  return (
    <Card
      className={clsx(classes.root, className)}
      {...rest}
    >
      <Box flexGrow={1}>
        <Typography
          color="inherit"
          component="h3"
          gutterBottom
          variant="overline"
        >
          {title}
        </Typography>
        <Box
          display="flex"
          alignItems="center"
          flexWrap="wrap"
        >
         <div className={classes.valueContainer}>
            <LoadingBox loading={tokenPriceLoading} p={0} size={'25px'} color="secondary">
              <Typography
                variant="h3"
                color="inherit"
              >
                {!isEmpty(tokenPrice) ? currency(getPriceBySymbol(tokenPrice, token.symbol)).format() : '-'}
              </Typography>
            </LoadingBox>
          </div>
        </Box>
      </Box>
      <Avatar
        className={classes.avatar}
        color="inherit"
      >
        <AttachMoneyIcon />
      </Avatar>
    </Card>
  );
};

RoiPerCustomer.propTypes = {
  className: PropTypes.string
};

export default RoiPerCustomer;
