import React from 'react';
import type { FC } from 'react';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import {
  Avatar,
  Box,
  Card,
  LinearProgress,
  Typography,
  makeStyles
} from '@material-ui/core';
import type { Theme } from 'src/theme';
import CircularProgress from './CircularProgress';
import Label from 'src/components/Label';
import { BigNumber } from 'ethers';
import { formatBalance } from 'src/utils/format';
import { useTranslation } from 'react-i18next';

interface TauBTCInitMiningProgressProps {
  className?: string;
  chefStats?:any;
  totalSupply?:any;
  initialMinted?:BigNumber;
}

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    padding: theme.spacing(3),
    // display: 'flex',
    // alignItems: 'center',
    // justifyContent: 'space-between'
  },
  progress: {
    margin: theme.spacing(0, 1),
    flexGrow: 1
  },
  label: {
    marginLeft: theme.spacing(1)
  },
  tau:{
    'text-transform': 'initial'
  }
}));

const TauBTCInitMiningProgress: FC<TauBTCInitMiningProgressProps> = ({totalSupply,initialMinted,chefStats, className, ...rest }) => {
  const classes = useStyles();
  const data = {
    target:2100,
    value: 0,
  };
  let percent = Math.round(data.value*100/data.target);
  // if (targetSupply && alreadyMined && alreadyAirdropped&& initialMinted){
  //   console.log(`balanceForChef:${balanceForChef},targetSupply:${targetSupply},alreadyMined:${alreadyMined},alreadyAirdropped:${alreadyAirdropped},initialMinted:${initialMinted}`)
  //   percent = (alreadyMined.add(alreadyAirdropped).add(initialMinted).sub(balanceForChef))
  //       .mul(10000).div(targetSupply).toNumber()/100;
  // }
  if (chefStats && chefStats.result && totalSupply && totalSupply.result){
    console.log(`balanceForChef:${chefStats.result.balanceForChef},total:${totalSupply.result},staked:${chefStats.result.stakedTBTC}`)
    const circulating = totalSupply.result.sub(chefStats.result.balanceForChef)
      .add(chefStats.result.stakedTBTC);

    percent = circulating.mul(10000).div(chefStats.result.target).toNumber()/100;
  }
  const {t} = useTranslation();
  return (
    <Card
      className={clsx(classes.root, className)}
      {...rest}
    >
      <Typography
        component="h3"
        gutterBottom
        variant="overline"
        color="textSecondary"
      >
        <i className={classes.tau}>&tau;</i>{t("stats.tbtc.miningProgress")}
      </Typography>
      <Box
        display="flex"
        alignItems="center"
        flexWrap="wrap"
      >
        <Typography
          variant="h3"
          color="textPrimary"
        >
          {percent}
          %
        </Typography>
        <LinearProgress
          className={classes.progress}
          value={percent}
          color="secondary"
          variant="determinate"
        />
      </Box>
    </Card>
  );
};

TauBTCInitMiningProgress.propTypes = {
  className: PropTypes.string
};

export default TauBTCInitMiningProgress;
